<template>
    <div>
        <h2>うみロググラフ</h2>
        <div class="wrapper1">
            <div class="calendar">
                <h3>{{ state }}</h3>
                <datepicker v-model="date" format="yyyy-MM-dd" :language="language" :inline="true" @selected="onDateSelected"></datepicker>
            </div>
            <div class="what">
                    <h4>どのデータを見ますか</h4>
                    <div class="btns-pc">
                        <button @click="scrollElement('oxygen')">溶存酸素</button>
                        <button @click="scrollElement('chloro')">クロロフィル</button>
                        <button @click="scrollElement('salinity')">塩分濃度</button>
                        <button @click="scrollElement('water_temp')">水温</button>
                        <button @click="scrollElement('turbidity')">濁度</button>
                    </div>
                    <div class="btns-sh">
                        <button @click="scrollElement('oxygen')">溶存<br>酸素</button>
                        <button @click="scrollElement('chloro')">クロロフ</button>
                        <button @click="scrollElement('salinity')">塩分<br>濃度</button>
                        <button @click="scrollElement('water_temp')">水温</button>
                        <button @click="scrollElement('turbidity')">濁度</button>
                    </div>
            </div>
        </div>
        <ChartTest/>
        <GoPagetop/>
    </div>
</template>

<script>
import {Component,Vue} from 'vue-property-decorator';
import AxiosTest from '@/components/ AxiosTest.vue';
import ChartTest from '@/components/ChartTest.vue';
import Datepicker from 'vuejs-datepicker';
import GoPagetop from '@/components/GoPagetop.vue';
import { ja } from 'vuejs-datepicker/dist/locale';

@Component({
    components:{
        AxiosTest,
        ChartTest,
        Datepicker,
        GoPagetop,
    }
})
export default class GetData extends Vue{

    date=new Date();
    language=ja;
    text="";
    axios_test=new AxiosTest();
    chart_test=new ChartTest();
    state="日付を選択後グラフを生成";

    week_label = [];
    month_label = [];


    mounted(){
        console.log("ページが読み込まれました");
        this.MakeLabel_week(this.date);
        this.MakeLabel_month(this.date);
        this.plot_data();
    }
    
    MakeLabel_week(date){
        // console.log(date);
        let lastWeek = new Date(date.getFullYear(), date.getMonth(), date.getDate()-6);
        // console.log(lastWeek);
        this.week_label = [];
        for (let week = lastWeek; week <= date; week.setDate(week.getDate() + 1)){
            this.week_label.push(new Date(week).getMonth()+1 + "/" + new Date(week).getDate());
        }
        // console.log(this.week_label);
    }
    MakeLabel_month(date){
        console.log(date);
        let lastMonth = new Date(date.getFullYear(), date.getMonth(), date.getDate()-29);
        console.log(lastMonth);
        this.month_label = [];
        for (let month = lastMonth; month <= date; month.setDate(month.getDate() + 1)){
            this.month_label.push(new Date(month).getMonth()+1 + "/" + new Date(month).getDate());
        }
        console.log(this.week_label);
    }
    async plot_data(){
        // if(this.chart_test.Oxygen || this.chart_test.Chloro || this.chart_test.Salinity || this.chart_test.Water_temp || this.chart_test.Turbidity){
        //     this.chart_test.Oxygen.destroy();
        //     this.chart_test.Chloro.destroy();
        //     this.chart_test.Salinity.destroy();
        //     this.chart_test.Water_temp.destroy();
        //     this.chart_test.Turbidity.destroy();
        //     this.chart_test.Oxygen_week.destroy();
        // }
        if(this.chart_test.Oxygen && this.chart_test.Oxygen_week && this.chart_test.Oxygen_month){
            this.chart_test.Oxygen.destroy();
            this.chart_test.Chloro.destroy();
            this.chart_test.Salinity.destroy();
            this.chart_test.Water_temp.destroy();
            this.chart_test.Turbidity.destroy();
            this.chart_test.Oxygen_week.destroy();
            this.chart_test.Chloro_week.destroy();
            this.chart_test.Salinity_week.destroy();
            this.chart_test.Water_temp_week.destroy();
            this.chart_test.Turbidity_week.destroy();
            this.chart_test.Oxygen_month.destroy();
            this.chart_test.Chloro_month.destroy();
            this.chart_test.Salinity_month.destroy();
            this.chart_test.Water_temp_month.destroy();
            this.chart_test.Turbidity_month.destroy();
        }
        console.log("clicked!");
        this.state = "生成中";
        await this.axios_test.get_day_data();
        this.chart_test.plot_day_data(this.axios_test.day_data);
        await this.axios_test.get_week_data();
        this.chart_test.plot_week_data(this.axios_test.week_12_data, this.week_label);
        await this.axios_test.get_month_data();
        this.chart_test.plot_month_data(this.axios_test.month_data, this.month_label);
        this.state = "日付を選択後グラフを生成";
    }

    onDateSelected(date){
        this.axios_test.date=date;
        this.plot_data();

        this.MakeLabel_week(date);
        this.MakeLabel_month(date);
    }

    scrollElement(name){
        const target = document.getElementById(name);
        target.scrollIntoView({behavior: "smooth", block: "start"});
    }
}
</script>
<style lang="scss" scoped>
h2 {
    @media screen and (max-width: 590px){
        font-size: 1.3rem;
    }
}
.wrapper1{
    padding: 0 100px 0 100px;
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: 590px) {
        padding: 0;
    }
    .calendar{
        width: 50%;
        @media screen and (max-width: 590px){
            width: 70%;
            margin-left: 3%;
        }
        .vdp-datepicker{
            display: block;
            text-align: center;
        }
    }
    .what{
        width: 50%;
        margin-left: 30px;
        @media screen and (max-width: 590px){
            width: 20%;
        }
        h4{
            @media screen and (max-width: 590px){
                display: none;
            }
        }
        .btns-pc{
            @media screen and (max-width: 590px){
                display: none;
            }
            button{
                display: block;
                padding: 9px 0;
                width: 60%;
                margin: 0 auto 16px auto;
            }
        }
        .btns-sh{
            display: none;
            @media screen and (max-width: 590px){
                display: block;
            }
            button{
                width: 55px;
                height: 55px;
                border-radius: 50%;
                margin-bottom: 17px;
            }
        }
    }
}
